import {Component, Input, OnInit, ViewChild, ViewContainerRef} from '@angular/core';
import {ItemInterface} from '../../core/models/item.model';
import {ItemService} from '../item.service';
import {RequestService} from '../../requests/request.service';
import {UserInterface} from '../../core/models/user.model';
import {UserService} from '../../user/user.service';
import {RequestStatus} from '../../requests/request.constants';
import {Router} from '@angular/router';
import {AuthService} from '../../auth/auth.service';

@Component({
    selector: 'app-item',
    templateUrl: './item.component.html',
    styleUrls: ['./item.component.css']
})

export class ItemComponent implements OnInit {
    @Input() item: ItemInterface;
    @Input() user: UserInterface;

    constructor(
        private itemService: ItemService,
        private requestService: RequestService,
        private userService: UserService,
        private authService: AuthService,
        private router: Router
    ) {}

    ngOnInit(): void {
        this.onRefresh();
    }

    private onRefresh() {
        this.getItem();
        this.getUser();
        console.log(this.item.images);
        // this.getItemStatus();
    }

    private onChange() {
        this.itemService.get(this.item.id).noCache().toPromise().then(data => this.item = data);
        this.userService.get(this.user.id).noCache().toPromise().then(data => this.user = data);
    }

    private getItem() {
        return this.item;
    }

    private getUser() {
        return this.user;
    }

    private onRequest() {
        // reset state of component and send success alert
        this.requestService.createByItem(this.item.id, {status: RequestStatus.STATUS_WAITING_FOR_APPROVAL, item: this.item.id})
            .subscribe((data) => {
                this.onChange();
                return data;
        });
    }

    private getItemStatus() {
        if (this.item.requests) {
            return this.item.requests.length;
        } else {
            return '-';
        }
    }

    private isRequested() {
        /** if item is already in current user requests */
        if (this.user.requests) {
            for (const request of this.user.requests) {
                console.log(request.id);
                for (const itemRequest of this.item.requests) {
                    if (itemRequest.id === request.id) {
                        return true;
                    }
                }
            }
        }
        return false;
    }

    private isAuthor() {
        if (this.item.user.id === this.authService.getUser().id) {
            return true;
        } else {
            return false;
        }
    }

    private onDelete(id: number) {
        this.itemService.delete(id).subscribe(() => {
            window.location.href = "http://localhost:4200/items";
        });
    }

    private getImage() {
        if (this.item) {
            if (this.item.images.length > 1) {
                console.log(this.item.images);
                return this.item.images[0].url;
            }
        }
    }
}
