import {Component, Input, OnInit, ViewChild, ViewEncapsulation} from '@angular/core';
import {ItemInterface} from '../../core/models/item.model';
import {ItemService} from '../item.service';
import {UserInterface} from '../../core/models/user.model';
import {UserService} from '../../user/user.service';
import {ActivatedRoute, Router} from '@angular/router';
import {AuthService} from '../../auth/auth.service';
import {MatDatepickerInputEvent} from '@angular/material';
import {RequestService} from '../../requests/request.service';
import {RequestStatus, RequestMessage} from '../../requests/request.constants';
import {RequestConstantsInterface, RequestInterface} from '../../core/models/request.model';
import {RequestHelpers} from '../../requests/request.helpers';

@Component({
    selector: 'app-review-item',
    templateUrl: './review-item.component.html',
    styleUrls: ['./review-item.component.css'],
})

export class ReviewItemComponent extends RequestHelpers implements OnInit {
    item: ItemInterface;
    user: UserInterface;
    itemStatus;
    date;
    dateChange = false;

    constructor(
        private itemService: ItemService,
        private userService: UserService,
        private authService: AuthService,
        private requestService: RequestService,
        private route: ActivatedRoute,
    ) {
        super();
    }

    ngOnInit(): void {
        this.onRefresh();
    }

    private onRefresh() {
        this.getItem();
        this.getUser();
        this.getItemStatus();
    }

    private onDateChange(event) {
        const date = event.target.value._i;
        this.date = date.year.toString() + '-' + date.month.toString() + '-' + date.date.toString();
    }

    private onSubmit(id) {
        this.requestService.accept(id, this.date).subscribe((data) => {
            this.onChange();
            return data;
        });
    }

    getItem() {
        const itemId = parseInt(this.route.snapshot.paramMap.get('item'));
        this.itemService.get(itemId).noCache().toPromise().then(data => this.item = data);
    }

    getUser() {
        const userId = this.authService.getUser().id;
        this.userService.get(userId).noCache().toPromise().then(data => this.user = data);
    }

    private onChange() {
        this.itemService.get(this.item.id).noCache().toPromise().then(data => this.item = data);
        this.userService.get(this.user.id).noCache().toPromise().then(data => this.user = data);
        this.getItem();
        this.getUser();
    }

    private getItemStatus() {
        if (this.item.requests) {
            return this.item.requests.length;
        } else {
            return '-';
        }
    }

    getDate(date) {
        if (date) {
            return date.substring(0, 10);
        } else {
            return 'Nepasirinkta';
        }
    }

    onEdit() {
        this.dateChange = true;
    }

    private onDateChangeSubmit(id) {
        this.requestService.updateDate(id, this.date).subscribe((data) => {
            this.onChange();
            this.dateChange = false;
            return data;
        });
    }

    onCancel(id: number) {
        this.requestService.cancel(id).subscribe((data) => {
            this.onChange();
            return data;
        });
    }

    private onDateChangeCancel() {
        this.dateChange = false;
        this.onChange();
    }

    private getReceivers() {
        const requests = this.item.requests;
        const users = [];

        if (this.itemStatus === this.STATUS_WAITING_FOR_APPROVAL) {
            for (const request of requests) {
                users.push(request.user);
            }
            return users;
        }

        for (const request of requests) {
            if (
                request.status === this.STATUS_ACCEPTED_BY_DEALER
                || request.status === this.STATUS_ACCEPTED_BY_DEALER
            ) {
                return [request.user];
            }
        }
    }
}
