import {Component, OnInit} from '@angular/core';
import {ItemInterface} from '../../core/models/item.model';
import {UserInterface} from '../../core/models/user.model';
import {AuthService} from '../../auth/auth.service';
import {ItemService} from '../item.service';
import {UserService} from '../../user/user.service';

@Component({
    selector: 'app-user-items',
    templateUrl: './user-items.component.html',
    styleUrls: ['./user-items.component.css']
})

export class UserItemsComponent implements OnInit {
    items: ItemInterface[];
    user: UserInterface;

    constructor(
        private authService: AuthService,
        private itemService: ItemService,
        private userService: UserService
    ) {}

    ngOnInit(): void {
        this.itemService.getByUser().noCache().toPromise().then(data => this.items = data);
        this.userService.get().noCache().toPromise().then(data => this.user = data);
    }
}
