import { Component } from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {Router} from '@angular/router';
import {UserService} from '../../user/user.service';
import {UserInterface} from '../../core/models/user.model';
import {AuthService} from '../../auth/auth.service';
import {ItemService} from '../item.service';
import {Observable} from 'rxjs';

@Component({
    selector: 'app-create-item',
    templateUrl: './create-item.component.html',
    styleUrls: ['./create-item.component.css']
})

export class CreateItemComponent {
    createItemForm: FormGroup;
    uploadableFiles;

    constructor(
        private fb: FormBuilder,
        private authService: AuthService,
        private itemService: ItemService,
        private router: Router,
    ) {
        this.createItemForm = new FormGroup({
            'title': new FormControl(null, Validators.required),
            'description': new FormControl(null, Validators.required),
            'images': new FormControl(null, Validators.required),
        });
    }

    get inputTitle() { return this.createItemForm.get('title'); }
    get inputDescription() { return this.createItemForm.get('description'); }
    get inputFile() { return this.createItemForm.get('images'); }

    onSubmit() {
        this.itemService.create(this.createItemForm.value, this.uploadableFiles).subscribe(({}) => {
            this.router.navigate(['/items']);
        }, (error) => {
            console.log('Error', error);
        });
    }

    fileChange(event) {
        const uploadableFiles = [];
        Array.from(event.target.files).forEach((file: File) => {
            uploadableFiles.push(file);
        });
        this.uploadableFiles = uploadableFiles;
    }
}
