import {Component, OnInit} from '@angular/core';
import {AuthService} from '../../auth/auth.service';
import {ItemService} from '../../items/item.service';
import {ItemInterface} from '../../core/models/item.model';
import {ActivatedRoute} from '@angular/router';
import {RequestHelpers} from '../../requests/request.helpers';

@Component({
    selector: 'app-items-panel',
    templateUrl: './items-panel.component.html',
    styleUrls: ['./items-panel.component.css']
})

export class ItemsPanelComponent extends RequestHelpers implements OnInit {
    items: ItemInterface[];

    constructor(
        private itemService: ItemService,
        private authService: AuthService,
        private route: ActivatedRoute
    ) {
        super();
    }

    ngOnInit(): void {
        this.itemService.all().subscribe((data) => {
            this.items = data;
        });
    }
}
