import {Component, Input, OnInit, ViewChild, ViewContainerRef} from '@angular/core';
import {Router} from '@angular/router';
import {ItemInterface} from '../../../core/models/item.model';
import {UserInterface} from '../../../core/models/user.model';
import {ItemService} from '../../../items/item.service';
import {RequestService} from '../../../requests/request.service';
import {UserService} from '../../../user/user.service';
import {RequestHelpers} from '../../../requests/request.helpers';
import {AuthService} from '../../../auth/auth.service';

@Component({
    selector: 'app-panel-item',
    templateUrl: './panel-item.component.html',
    styleUrls: ['./panel-item.component.css']
})

export class PanelItemComponent extends RequestHelpers implements OnInit {
    @Input() item: ItemInterface;
    dateChange;
    user;
    date;
    itemStatus;

    constructor(
        private itemService: ItemService,
        private requestService: RequestService,
        private userService: UserService,
        private router: Router,
        private authService: AuthService
    ) {
        super();
    }

    private onRefresh() {
        this.getItem();
        this.getUser();
        this.getItemStatus();
    }

    ngOnInit(): void {
        this.onRefresh();
    }

    private getReceivers() {
        const requests = this.item.requests;
        const users = [];

        if (this.itemStatus === this.STATUS_WAITING_FOR_APPROVAL) {
            for (const request of requests) {
                users.push(request.user);
            }
            return users;
        }

        for (const request of requests) {
            if (
                request.status === this.STATUS_ACCEPTED_BY_DEALER
                || request.status === this.STATUS_ACCEPTED_BY_DEALER
            ) {
                return [request.user];
            }
        }
    }

    getItem() {
        this.itemService.get(this.item.id).noCache().toPromise().then(data => this.item = data);
    }

    private onDateChange(event) {
        const date = event.target.value._i;
        this.date = date.year.toString() + '-' + date.month.toString() + '-' + date.date.toString();
    }

    private onSubmit(id) {
        this.requestService.approve(id).subscribe((data) => {
            this.onChange();
            return data;
        });
    }

    getUser() {
        const userId = this.authService.getUser().id;
        this.userService.get(userId).noCache().toPromise().then(data => this.user = data);
    }

    private onChange() {
        this.itemService.get(this.item.id).noCache().toPromise().then(data => this.item = data);
        this.userService.get(this.user.id).noCache().toPromise().then(data => this.user = data);
        this.getItem();
        this.getUser();
    }

    private getItemStatus() {
        this.itemStatus = this.item.requests.length;
    }

    getDate(date) {
        if (date) {
            return date.substring(0, 10);
        } else {
            return 'Nepasirinkta';
        }
    }

    onEdit() {
        this.dateChange = true;
    }

    private onDateChangeSubmit(id) {
        this.requestService.updateDate(id, this.date).subscribe((data) => {
            this.onChange();
            this.dateChange = false;
            return data;
        });
    }

    onCancel(id: number) {
        this.requestService.cancel(id).subscribe((data) => {
            this.onChange();
            return data;
        });
    }

    private onDateChangeCancel() {
        this.dateChange = false;
        this.onChange();
    }
}
