import {Injectable} from '@angular/core';
import {ApiService} from '../api.service';
import {CacheableObservable} from '../cacheable-observable/cacheable-observable.model';
import {ItemInterface} from '../core/models/item.model';
import {map} from 'rxjs/operators';

@Injectable()
export class ItemService {
    constructor(
        private api: ApiService,
    ) {}

    create(body, files?) {
        const headers = new Headers();
        headers.append('Content-Type', 'multipart/form-data');
        headers.append('Accept', 'application/json');

        return this.api.upload('items/new', {title: body.title, description: body.description}, files);
    }

    all(): CacheableObservable<ItemInterface[]> {
        return this.api.get(`items/`).pipe(map(({data}) => data)) as CacheableObservable<ItemInterface[]>;
    }

    get(id: number): CacheableObservable<ItemInterface> {
        return this.api.get(`items/${id}`).pipe(map(({data}) => data)) as CacheableObservable<ItemInterface>;
    }

    requestedItemsByUser(id: number): CacheableObservable<ItemInterface[]> {
        return this.api.get(`items/${id}/user/requests`).pipe(map(({data}) => data)) as CacheableObservable<ItemInterface[]>;
    }

    getByUser(): CacheableObservable<ItemInterface[]> {
        return this.api.get(`items/user`).pipe(map(({data}) => data)) as CacheableObservable<ItemInterface[]>;
    }

    delete(id: number) {
        return this.api.delete(`items/${id}/delete`, null).pipe(map(({data}) => data));
    }

    getByStatus(status: number) {
        return this.api.get('items/status', {status: status});
    }
}
