import {Component, OnInit} from '@angular/core';
import {AuthService} from '../auth/auth.service';
import {ItemInterface} from '../core/models/item.model';
import {UserInterface} from '../core/models/user.model';
import {UserService} from '../user/user.service';
import {RequestService} from './request.service';
import {RequestInterface} from '../core/models/request.model';
import {ItemService} from '../items/item.service';

@Component({
    selector: 'app-requests',
    templateUrl: './requests.component.html',
    styleUrls: ['./requests.component.css']
})

export class RequestsComponent implements OnInit {
    items: ItemInterface[];
    requests: RequestInterface[];
    user: UserInterface;

    constructor(
        private authService: AuthService,
        private itemService: ItemService,
        private requestService: RequestService,
        private userService: UserService
    ) {}

    ngOnInit(): void {
        this.userService.get().noCache().toPromise().then(data => this.user = data);
        this.itemService.requestedItemsByUser(this.authService.getUser().id).noCache().toPromise().then(data => this.items = data);
        this.requestService.getByUser(this.authService.getUser().id).noCache().toPromise().then(data => this.requests = data);
    }
}
