import {Injectable} from '@angular/core';
import {ApiService} from '../api.service';
import {Router} from '@angular/router';
import {CacheableObservable} from '../cacheable-observable/cacheable-observable.model';
import {ItemInterface} from '../core/models/item.model';
import {map} from 'rxjs/operators';
import {RequestStatus} from './request.constants';

@Injectable()
export class RequestService {
    constructor(
        private api: ApiService,
    ) {

    }

    createByItem(id: number, itemRequest) {
        return this.api.post(`requests/${id}/item`, itemRequest);
    }

    getByUser(id: number) {
        return this.api.get(`requests/${id}/user`);
    }

    getByItemAndUser(itemId: number, userId: number) {
        return this.api.get(`requests/${itemId}/item/${userId}/user`);
    }

    delete(id: number) {
        return this.api.delete(`requests/${id}/delete`, null);
    }

    accept(id: number, date) {
        return this.api.post(`requests/${id}/accept`, {status: RequestStatus.STATUS_ACCEPTED_BY_DEALER, deliveryDate: date});
    }

    approve(id: number) {
        return this.api.post(`requests/${id}/accept`, {status: RequestStatus.STATUS_APPROVED});
    }

    cancel(id: number) {
        return this.api.post(`requests/${id}/accept`, {status: RequestStatus.STATUS_CANCELED});
    }

    updateDate(id: number, date) {
        return this.api.post(`requests/${id}/accept`, {status: RequestStatus.STATUS_ACCEPTED_BY_DEALER, deliveryDate: date});
    }
}
