import {Component, OnInit} from '@angular/core';
import {RequestHelpers} from '../../../requests/request.helpers';
import {ItemInterface} from '../../../core/models/item.model';
import {ItemService} from '../../../items/item.service';

@Component({
    selector: 'app-accepted-by-dealer',
    templateUrl: './accepted-by-dealer.component.html',
    styleUrls: ['./accepted-by-dealer.component.css']
})

export class AcceptedByDealerComponent extends RequestHelpers implements OnInit {
    items: ItemInterface[];

    constructor(
        private itemService: ItemService,
    ) {
        super();
    }

    ngOnInit(): void {
       this.getItems();
    }

    getItems() {
        this.itemService.getByStatus(this.STATUS_ACCEPTED_BY_DEALER).subscribe((data) => {
            this.items = data.data;
        });
    }

    onClick() {
        this.getItems();
    }
}
