import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { MDBBootstrapModule } from 'angular-bootstrap-md';

import { AppComponent } from './app.component';
import {Landing} from './landing/landing.component';
import {Header} from './header/header.component';
import {Login} from './auth/login/login.component';
import {Register} from './auth/register/register.component';
import {Nav} from './nav/nav.component';
import { RouterModule, Routes } from '@angular/router';
// MDB Angular Free
import { ModalModule, TooltipModule, PopoverModule, ButtonsModule } from 'angular-bootstrap-md';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {AuthService} from './auth/auth.service';
import {ApiService} from './api.service';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import {UserService} from './user/user.service';
import {LogoutComponent} from './auth/logout/logout.component';
import {CreateItemComponent} from './items/create-item/create-item.component';
import {ItemService} from './items/item.service';
import {AuthInterceptor} from './auth/auth.interceptor';
import {ItemsComponent} from './items/items.component';
import {ItemComponent} from './items/item/item.component';
import {RequestService} from './requests/request.service';
import {RequestComponent} from './requests/request/request.component';
import {RequestsComponent} from './requests/requests.component';
import {UserItemsComponent} from './items/user-items/user-items.component';
import {ReviewItemComponent} from './items/review-item/review-item.component';
import {
  MatInputModule,
  MatButtonModule,
  MatSelectModule,
  MatIconModule,
  MAT_DATE_LOCALE,
  MatDatepickerModule,
  MatNativeDateModule, MatCalendarHeader, matDatepickerAnimations, MatRadioModule, MAT_DATE_FORMATS, DateAdapter, MatToolbarModule
} from '@angular/material';
import { MomentDateAdapter } from '@angular/material-moment-adapter';

import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import {RequestedUserItemsComponent} from './items/requested-user-items/requested-user-items.component';
import {ItemsPanelComponent} from './admin/items-panel/items-panel.component';
import {WaitingForApprovalComponent} from './admin/items-panel/waiting-for-approval/waiting-for-approval.component';
import {AcceptedByDealerComponent} from './admin/items-panel/accepted-by-dealer/accepted-by-dealer.component';
import {ApprovedComponent} from './admin/items-panel/approved/approved.component';
import {PanelItemComponent} from './admin/items-panel/panel-item/panel-item.component';


const appRoutes: Routes = [
  { path: 'register', component: Register },
  { path: 'login', component: Login },
  { path: 'logout', component: LogoutComponent },
  { path: 'items/new', component: CreateItemComponent },
  { path: 'items', component: ItemsComponent },
  { path: 'items/user', component: UserItemsComponent },
  { path: 'items/user/requests', component: RequestedUserItemsComponent },
  { path: 'items/:item', component: ReviewItemComponent },
  { path: 'requests', component: RequestsComponent },
  { path: 'requests/:request', component: RequestComponent },
  { path: '', component: Landing },
  // { path: '**', component: PageNotFoundComponent }

  { path: 'admin/items', component: ItemsPanelComponent },
  { path: 'admin/items/waiting', component: WaitingForApprovalComponent },
  { path: 'admin/items/accepted', component: AcceptedByDealerComponent },
  { path: 'admin/items/approved', component: ApprovedComponent },
];

export const MY_FORMATS = {
  parse: {
    dateInput: 'LL',
  },
  display: {
    dateInput: 'YYYY-MM-DD',
    monthYearLabel: 'YYYY',
    dateA11yLabel: 'LL',
    monthYearA11yLabel: 'YYYY',
  },
};

@NgModule({
  declarations: [
    AppComponent,
    Landing,
    Header,
    Login,
    Register,
    Nav,
    LogoutComponent,
    CreateItemComponent,
    ItemsComponent,
    ItemComponent,
    RequestsComponent,
    RequestComponent,
    UserItemsComponent,
    ReviewItemComponent,
    RequestedUserItemsComponent,
    ItemsPanelComponent,
    WaitingForApprovalComponent,
    AcceptedByDealerComponent,
    ApprovedComponent,
    PanelItemComponent
  ],
  imports: [
    RouterModule.forRoot(
        appRoutes,
        {enableTracing: false} // <-- debugging purposes only
    ),
    BrowserModule,
    MDBBootstrapModule.forRoot(),
    ModalModule,
    TooltipModule,
    PopoverModule,
    ButtonsModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    BrowserAnimationsModule,
    MatInputModule,
    MatButtonModule,
    MatSelectModule,
    MatIconModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatRadioModule,
    MatToolbarModule
  ],

  providers: [
      AuthService,
      ApiService,
      UserService,
      ItemService,
      RequestService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptor,
      multi: true
    },
    {provide: MAT_DATE_LOCALE, useValue: 'lt-LT'},
    {provide: DateAdapter, useClass: MomentDateAdapter, deps: [MAT_DATE_LOCALE]},
    {provide: MAT_DATE_FORMATS, useValue: MY_FORMATS},
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
