import {Component, Input, OnInit} from '@angular/core';
import {ItemInterface} from '../../core/models/item.model';
import {UserInterface} from '../../core/models/user.model';
import {UserService} from '../../user/user.service';
import {RequestInterface} from '../../core/models/request.model';
import {ItemService} from '../../items/item.service';
import {RequestService} from '../request.service';
import {RequestStatus, RequestMessage} from '../request.constants';

@Component({
    selector: 'app-request',
    templateUrl: './request.component.html',
    styleUrls: ['./request.component.css']
})

export class RequestComponent implements OnInit {
    _ref: any;
    @Input() item: ItemInterface;
    @Input() request: RequestInterface;
    @Input() user: UserInterface;

    constructor(
        private itemService: ItemService,
        private requestService: RequestService,
        private userService: UserService
    ) {}

    ngOnInit(): void {
        this.onRefresh();
    }

    private onRefresh() {
        this.getItem();
        this.getUser();
        this.getRequest();
    }

    private onChange() {
        this.userService.get(this.user.id).noCache().toPromise().then(data => this.user = data);
        this.itemService.get(this.item.id).noCache().toPromise().then(data => this.item = data);
    }

    private getItem() {
        return this.item;
    }

    private getUser() {
        return this.user;
    }

    private getRequest() {
        for (const request of this.user.requests) {
            for (const itemRequest of this.item.requests) {
                if (itemRequest.id === request.id) {
                    this.request = request;
                }
            }
        }
    }

    private getItemStatus() {
        switch (this.request.status) {
            case RequestStatus.STATUS_WAITING_FOR_APPROVAL:
                return RequestMessage.STATUS_WAITING_FOR_APPROVAL;
            case RequestStatus.STATUS_ACCEPTED_BY_DEALER:
                return RequestMessage.STATUS_ACCEPTED_BY_DEALER;
            case RequestStatus.STATUS_APPROVED:
                return RequestMessage.STATUS_APPROVED;
            case RequestStatus.STATUS_SHIPPED:
                return RequestMessage.STATUS_SHIPPED;
            case RequestStatus.STATUS_COMPLETED:
                return RequestMessage.STATUS_COMPLETED;
            case RequestStatus.STATUS_DELETED:
                return RequestMessage.STATUS_DELETED;
            default:
                return '-';
        }
    }

    private onRemove() {
        /** if item is already in current user requests */
        this.requestService.delete(this.request.id).toPromise()
            .then(this.onChange)
            .then(this._ref.destroy());
    }

}
