import {Component, OnInit} from '@angular/core';
import {RequestHelpers} from '../../../requests/request.helpers';
import {ItemInterface} from '../../../core/models/item.model';
import {ItemService} from '../../../items/item.service';

@Component({
    selector: 'app-approved',
    templateUrl: './approved.component.html',
    styleUrls: ['./approved.component.css']
})

export class ApprovedComponent extends RequestHelpers implements OnInit {
    items: ItemInterface[];

    constructor(
        private itemService: ItemService,
    ) {
        super();
    }

    ngOnInit(): void {
        this.getItems();
    }

    getItems() {
        this.itemService.getByStatus(this.STATUS_APPROVED).subscribe((data) => {
            this.items = data.data;
        });
    }

    onClick() {
        this.getItems();
    }
}
