import {Component, OnInit} from '@angular/core';
import {RequestHelpers} from '../../../requests/request.helpers';
import {ItemInterface} from '../../../core/models/item.model';
import {ItemService} from '../../../items/item.service';

@Component({
    selector: 'app-waiting-for-approval',
    templateUrl: './waiting-for-approval.component.html',
    styleUrls: ['./waiting-for-approval.component.css']
})

export class WaitingForApprovalComponent extends RequestHelpers implements OnInit {
    items: ItemInterface[];

    constructor(
        private itemService: ItemService,
    ) {
        super();
    }

    ngOnInit(): void {
        this.getItems();
    }

    getItems() {
        this.itemService.getByStatus(this.STATUS_WAITING_FOR_APPROVAL).subscribe((data) => {
            this.items = data.data;
        });
    }

    onClick() {
        this.getItems();
    }

    waiting() {
        this.itemService.getByStatus(this.STATUS_WAITING_FOR_APPROVAL).subscribe((data) => {
            this.items = data.data;
        });
    }
}
