import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {AuthService} from '../auth.service';
import {Router} from '@angular/router';

@Component({
    selector: 'app-register',
    templateUrl: './register.component.html',
    styleUrls: ['./register.component.css']
})

export class Register {
    registerFrom: FormGroup;
    loading = false;
    submitted = false;

    constructor(private fb: FormBuilder, private authService: AuthService, private router: Router) {
        this.registerFrom = new FormGroup({
            'firstName': new FormControl(null, Validators.required),
            'lastName': new FormControl(null, Validators.required),
            'email': new FormControl(null, [Validators.required, Validators.email]),
            'phone': new FormControl(null, Validators.required),
            'address': new FormControl(null, Validators.required),
            'city': new FormControl(null, Validators.required),
            'plainPassword': new FormControl(null, Validators.required),
            'role': new FormControl(null, Validators.required)
        });
    }

    get inputFirstName() { return this.registerFrom.get('firstName'); }
    get inputLastName() { return this.registerFrom.get('lastName'); }
    get inputEmail() { return this.registerFrom.get('email'); }
    get inputPassword() { return this.registerFrom.get('plainPassword'); }
    get inputPhone() { return this.registerFrom.get('phone'); }
    get inputAddress() { return this.registerFrom.get('address'); }
    get inputCity() { return this.registerFrom.get('city'); }
    get inputRole() { return this.registerFrom.get('role'); }

    onSubmit() {
        this.authService.register(this.registerFrom.value).subscribe(() => {
            this.registerFrom.reset();
            this.router.navigate(['/login']);
        }, error => {
            console.log('Error', error);
        });
    }
}
