import {Injectable} from '@angular/core';
import {ApiService} from '../api.service';
import {Router} from '@angular/router';
import {map, tap} from 'rxjs/operators';
import {BehaviorSubject, Observable} from 'rxjs';
import {UserInterface} from '../core/models/user.model';
import {AuthConstants} from './auth.constants';

@Injectable()
export class AuthService {
    private token: string;

    constructor(
        private api: ApiService,
        private router: Router
    ) {
        const currentUser: UserInterface = this.getUser();
        this.token = currentUser ? currentUser.token : null;
    }
    register(body) {
        return this.api.post(AuthConstants.REGISTER_PATH, body);
    }
    login(body) {
        return this.api.post(AuthConstants.LOGIN_PATH, {_username: body.email, _password: body.plainPassword}).pipe(
            map(({data, token}) => {
                if (!token) {
                    throw new Error('Valid token not provided');
                }
                const role = data.role;
                const externalRedirect = data.redirect;
                const id = data.id;
                return {externalRedirect, token, role, id};
            }),
            tap(({externalRedirect, token, role, id}) => {
                this.token = token;
                localStorage.setItem(AuthConstants.LOCAL_STORAGE_KEY, JSON.stringify(
                    {token, role, redirect: externalRedirect, id})
                );
            }),
        );
    }
    static getRealUser(): UserInterface {
        return JSON.parse(localStorage.getItem(AuthConstants.LOCAL_STORAGE_KEY)) || null;
    }

    static getToken(): string {
        const user: UserInterface = AuthService.getRealUser();
        return user && user.token ? user.token : null;
    }

    isAuthenticated(): boolean {
        const user = this.getUser();
        return !!user;
    }

    // authenticated(authenticated: boolean) {
    //     this.authenticated$.next(authenticated);
    // }
    //
    // authenticatedObservable(): Observable<boolean> {
    //     return this.authenticated$.asObservable();
    // }
    //
    getUser(): UserInterface {
        return AuthService.getRealUser();
    }

    updateUser(user: UserInterface) {
        const currentUser: UserInterface = this.getUser();
        const updatedUser: UserInterface = {...currentUser, ...user};

        localStorage.setItem(AuthConstants.LOCAL_STORAGE_KEY, JSON.stringify(updatedUser));
    }

    logout() {
        localStorage.removeItem(AuthConstants.LOCAL_STORAGE_KEY);
        this.api.clearCache();
        window.location.href = "http://localhost:4200";
    }
}
