import { Component } from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {AuthService} from '../auth.service';
import {Router} from '@angular/router';
import {UserService} from '../../user/user.service';

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.css']
})

export class Login {
    loginForm: FormGroup;

    constructor(
        private fb: FormBuilder,
        private authService: AuthService,
        private router: Router,
        private userService: UserService
    ) {
        this.loginForm = new FormGroup({
            'email': new FormControl(null, [Validators.required, Validators.email]),
            'plainPassword': new FormControl(null, Validators.required),
        });
    }

    get inputEmail() { return this.loginForm.get('email'); }
    get inputPassword() { return this.loginForm.get('plainPassword'); }

    onSubmit() {
        this.authService.login(this.loginForm.value).subscribe(({externalRedirect, role, token}) => {
            window.location.href = "http://localhost:4200";
        }, (error) => {
            console.log('Error', error);
        });
    }
}
